const DEFAULT_NETWORK = FTM_TEST;

export const RPC_URL = config.rpcUrl[DEFAULT_NETWORK];
export const CHAIN_ID = config.chainId[DEFAULT_NETWORK];
export const SCAN = config.scan[DEFAULT_NETWORK];

export const getChainData = (chainId: number): any => {
  switch (chainId) {
    case FTM_MAIN:
      return {
        chainId: toHex(FTM_MAIN),
        chainName: "Fantom",
        nativeCurrency: { name: "Fantom", symbol: "FTM", decimals: 18 },
        rpcUrls: [config.rpcUrl[FTM_MAIN]],
        blockExplorerUrls: [config.scan[FTM_MAIN]],
      };
    case FTM_TEST:
      return {
        chainId: toHex(FTM_TEST),
        chainName: "Fantom Testnet",
        nativeCurrency: { name: "Fantom", symbol: "FTM", decimals: 18 },
        rpcUrls: [config.rpcUrl[FTM_TEST]],
        blockExplorerUrls: [config.scan[FTM_TEST]],
      };
    case POLYGON:
      return {
        chainId: toHex(POLYGON),
        chainName: "Polygon",
        nativeCurrency: { name: "Matic", symbol: "MATIC", decimals: 18 },
        rpcUrls: [config.rpcUrl[POLYGON]],
        blockExplorerUrls: [config.scan[POLYGON]],
      };
    case AVAX:
      return {
        chainId: toHex(AVAX),
        chainName: "Avalanche",
        nativeCurrency: { name: "Avax", symbol: "AVAX", decimals: 18 },
        rpcUrls: [config.rpcUrl[AVAX]],
        blockExplorerUrls: [config.scan[AVAX]],
      };
    default:
      return {
        chainId: toHex(chainId),
        chainName: undefined,
        nativeCurrency: undefined,
        rpcUrls: undefined,
        blockExplorerUrls: undefined,
      };
  }
};

// export const CHAIN_DATA = getChainData(CHAIN_ID);

export const getAddress = (
  obj: {
    addresses: { [_: string]: string };
  },
  chainId: number
): string => {
  return obj.addresses[chainId];
};

export const getScan = (chainId: number): string => {
  return config.scan[chainId];
};
