import { createContext, ReactNode, useCallback, useEffect, useState } from "react";
import { ethers } from "ethers";
import { isApprovedChain } from "../util/misc";

export const Web3Context = createContext<{
  provider: ethers.providers.Web3Provider;
  chainId: number;
  account: string;
  ethBalance: string;
  getAddress: Function;
}>({
  provider: null,
  chainId: null,
  account: null,
  ethBalance: null,
  getAddress: null,
});

export const Web3ContextProvider = ({ children }: { children: ReactNode }) => {
  const [provider, setProvider] = useState<ethers.providers.Web3Provider>(null);
  const [chainId, setChainId] = useState<number>(null);
  const [account, setAccount] = useState<string>(null);
  const [ethBalance, setEthBalance] = useState<string>(null);

  const getAddress = useCallback(
    (obj: { addresses: { [_: string]: string } }): string => {
      return obj.addresses[chainId];
    },
    [chainId]
  );

  useEffect(() => {
    (async () => {
      // await window.ethereum.enable();

      if (!window.ethereum) {
        alert("MetaMask is not installed. Please consider installing it: https://metamask.io/download.html");
        return;
      }

      window.ethereum.on("accountsChanged", (accounts: string[]) => {
        // console.log("account changed:", accounts[0]);
        window.location.reload();
        // setAccount(accounts[0]);
      });

      window.ethereum.on("chainChanged", (chainId: number[]) => {
        console.log("Changed chain to:", chainId);
        window.location.reload();
      });

      const provider = new ethers.providers.Web3Provider(window.ethereum);
      setProvider(provider);
    })();
  }, []);

  useEffect(() => {
    if (chainId == null) return;
    if (!isApprovedChain(chainId)) {
      console.log(`Chain ID is ${chainId}. Please change network.`);
    }
    // if (chainId !== CHAIN_ID) {
    //   console.log(`Chain ID is ${chainId}. Please change to the right network.`);
    // }
  }, [chainId]);

  // This only happens once
  useEffect(() => {
    (async () => {
      if (provider == null) return;

      const chainId = (await provider.getNetwork()).chainId;
      // if (chainId !== CHAIN_ID) return;
      if (!isApprovedChain(chainId)) return;
      setChainId(chainId);

      const account = (await provider.listAccounts())[0];
      if (account != null) {
        setAccount(account);
      } else {
        return console.log("Connect to MetaMask to use this website.");
      }
    })();
  }, [provider, chainId]);

  useEffect(() => {
    (async () => {
      if (provider == null || account == null) {
        setEthBalance(null);
        return;
      }
      try {
        setEthBalance((await provider.getBalance(account)).toString());
      } catch (err) {
        console.log(err);
      }
    })();
  }, [provider, account]);

  return (
    <Web3Context.Provider
      value={{
        provider,
        chainId,
        account,
        ethBalance,
        getAddress,
      }}
    >
      {children}
    </Web3Context.Provider>
  );
};
