import { ethers } from "ethers";
import { useContext, useEffect, useState } from "react";
import { Web3Context } from "../contexts/Web3Context";
import { UserContext } from "../contexts/UserContext";
import { A } from "../components/A";
import { addToken, isApprovedChain, onClickConnect, shortenAddress, switchChain } from "../util/misc";
import {
  AVAX,
  contracts,
  FTM_MAIN,
  FTM_TEST,
  getAddress,
  getScan,
  POLYGON,
  REWARD_TOKEN_SYMBOL,
} from "../config/constants";
import { tokens } from "../config/tokens";

import banner from "../resources/img/potluck-labs.png";
import iconFTM from "../resources/img/tokens/FTM.png";
import iconMATIC from "../resources/img/tokens/MATIC.png";
import iconAVAX from "../resources/img/tokens/AVAX.png";

const f = (x: ethers.BigNumberish, units = 18): string => ethers.utils.formatUnits(x.toString(), units);
// const p = (x: string, units = 18): ethers.BigNumber => ethers.utils.parseUnits(x, units);
// const c = (x: number, d = 0) => ethers.utils.commify(x.toFixed(d));
const c = (x: string, d = 0) => ethers.utils.commify(parseFloat(x).toFixed(d));

export const Home = () => {
  const { account, provider, chainId } = useContext(Web3Context);
  const { pendingReward, ownedTokens, nfts, claim } = useContext(UserContext);

  const [loaded, setLoaded] = useState(false);

  const [needsUpdate, setNeedsUpdate] = useState(null);

  useEffect(() => {
    if (nfts == null) return;
    if (ownedTokens == null) return;

    for (const key in nfts) {
      const nft = nfts[key];
      const cs = ownedTokens[nft.address].checkpoints;
      for (const c of cs) {
        if (c === "0") {
          setNeedsUpdate(true);
          return;
        }
      }
    }
    setNeedsUpdate(false);
  }, [nfts, ownedTokens]);

  useEffect(() => {
    if (pendingReward == null) return;
    if (ownedTokens == null) return;
    if (nfts == null) return;
    setLoaded(true);
  }, [pendingReward, ownedTokens, nfts]);

  // if (!loaded) {
  //   return <div>Loading...</div>;
  // }

  const WrongNetwork = () => {
    return (
      <div>
        <br />
        To begin, please connect to a supported chain:
        <br />
        <br />
        <NetworkOptions />
      </div>
    );
  };

  const NotConnected = () => {
    return (
      <div>
        <button className="button" onClick={() => onClickConnect(provider, chainId, account)}>
          Connect Wallet
        </button>
        <br />
        <br />
        <NetworkOptions />
        <br />
      </div>
    );
  };

  const Connected = () => {
    if (!loaded) {
      return <div>Loading...</div>;
    }
    return (
      <div>
        <button className="button" onClick={() => claim()}>
          {needsUpdate && "Update &"} Claim
          <br />
          <span className="button-amount">{c(f(pendingReward), 4)} $LABS</span>
        </button>

        <div>
          Connected as <A href={`${getScan(chainId)}/address/${account}`}>{shortenAddress(account, 4)}</A>
        </div>

        <br />
        <NetworkOptions />
        <br />

        <div>
          <span className="add-to-metamask" onClick={() => addToken(tokens[REWARD_TOKEN_SYMBOL], chainId)}>
            Add $LABS to MetaMask
          </span>
        </div>

        <div className="info">
          <h2>Approved NFT collections</h2>
          <p>
            Here you see which NFT collections you are able to earn rewards for, and which NFTs you own in each
            collection (by token ID).
          </p>
        </div>

        {needsUpdate && (
          <div className="have-untracked">
            You have untracked NFTs!
            <br />
            Press the "Update &amp; Claim" button to register them.
          </div>
        )}

        <div className="nfts">
          {Object.values(nfts).map((nft) => (
            <div className="nft" key={nft.address}>
              <div className="nft-name-symbol">
                {nft.name} ({nft.symbol})
              </div>

              <div className="nft-address">
                <A href={`${getScan(chainId)}/token/${nft.address}`}>{shortenAddress(nft.address, 4)}</A>
              </div>

              <div>Reward multiplier: {nfts[nft.address].weight}x</div>
              <br />

              <div>
                {ownedTokens[nft.address].tokenIds.map((x: any, i: number) => (
                  <div key={x}>
                    &nbsp; {ownedTokens[nft.address].checkpoints[i] === "0" ? "⚠️" : "✅"} Token {x}
                  </div>
                ))}
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  };

  const NetworkOptions = () => {
    return (
      <div className="network-options">
        <span className={chainId === FTM_MAIN ? "selected" : ""} onClick={() => switchChain(FTM_MAIN)}>
          <img src={iconFTM} alt="FTM" /> Fantom
        </span>
        {/* <span className={chainId === FTM_TEST ? "selected" : ""} onClick={() => switchChain(FTM_TEST)}>
          <img src={iconFTM} alt="Fantom" /> Fantom Testnet
        </span> */}
        <span className={chainId === POLYGON ? "selected" : ""} onClick={() => switchChain(POLYGON)}>
          <img src={iconMATIC} alt="Polygon" /> Polygon
        </span>
        <span className={chainId === AVAX ? "selected" : ""} onClick={() => switchChain(AVAX)}>
          <img src={iconAVAX} alt="Avalanche" /> Avalanche
        </span>
      </div>
    );
  };

  return (
    <div className="home">
      <div className="wrapper">
        <img className="banner" src={banner} alt="Banner" />
        <div className="info">
          <h2>Hold NFTs, earn $LABS.</h2>
          <p>
            For every NFT you hold from a Potluck Labs-approved collection, you gain some $LABS (Potluck Labs Token)
            every second.
          </p>
          {isApprovedChain(chainId) && account && (
            <p>In order to start earning rewards for an NFT, register it by pressing the button below.</p>
          )}
        </div>

        {/* <div className="connection">
          {!isApprovedChain(chainId) ? <WrongNetwork /> : account ? <Connected /> : <NotConnected />}
        </div> */}
        <div className="connection">
          {!isApprovedChain(chainId) ? <WrongNetwork /> : account ? <Connected /> : <NotConnected />}
        </div>

        <div className="contracts">
          Contracts:{" "}
          <A href={`${getScan(chainId)}/address/${getAddress(contracts[REWARD_TOKEN_SYMBOL], chainId)}`}>LABS</A> |{" "}
          <A href={`${getScan(chainId)}/address/${getAddress(contracts.rewarder, chainId)}`}>Rewarder</A>
        </div>
      </div>
    </div>
  );
};
